



































import uniIcon from "@/components/uni-icon.vue";
	export default {
		data() {
			return {
				name:'',
				pwd:'',
				show:true,
			}
		},
		components: {
			uniIcon,
		},
		methods:{
			login() {//登录
			uni.navigateTo({
				url: '../index/index'
			})  
			},
			test() {//登录
			uni.navigateTo({
				url: '../index/index'
			})  
			},
			register(){//注册
				uni.navigateTo({
					url:'../register/register'
				})
			},
			goLogin() {
				if (this.name.length <= 0){
					uni.showToast({
						icon:'none',
						title:"请输入用户名"
					});
					return;
				}
				if (this.pwd.length <= 0){
					uni.showToast({
						icon:'none',
						title:"请输入密码"
					});
					return;
				}
				uni.request({
					url: 'http://yijint.top:8089/login',//开发者服务器接口地址
					method: 'POST',//get或post
					data: {//请求的参数
						username:this.name,
						password:this.pwd
					},
					success: res => {
						//console.log(this.name)
						console.log(res.data)
						uni.showToast({//交互反馈接口，显示消息提示框。
							icon:'none',
							title:'登录成功'
						})
					},
					fail: () => {
						uni.hideLoading();//隐藏 loading 提示框。
						console.log("请求失败")
						//console.log(this.name)
						uni.showToast({//交互反馈接口，显示消息提示框。
							icon:'none',
							title:'请求失败'
						})
					},
					complete: () => {}
				});
			},
			gettest(){
				uni.navigateTo({//登录成功页面跳转
					url:'../222/222'
				})
			},
			WX_MP_getuserinfo(e){
				//微信小程序微信登录方法	
				uni.login({
				provider: 'weixin',
				success: function (loginRes) {
				//登录成功
				uni.getUserInfo({
					success: function (res){
						const userInfo = res.userInfo
						const nickName= userInfo.nickName//读取头像
						const avatarUrl= userInfo.avatarUrl
						uni.setStorageSync('wxname', nickName)//缓存
						uni.setStorageSync('wximg', avatarUrl)
						
					}	
				});
				uni.navigateTo({//登录成功页面跳转
					url:'../register/register'
				})
					},
					fail:function(){	
					}
				});
			}
		},
		onLoad(){//第一次加载
		},
		beforeUpdate(){//数据更新时调用此生命周期函数
			
		},
	}
